const Page2 = () => {

  const [priceForNft, setpriceForNft] = useState(0);
  const [value, setvalue] = useState(0);
  const { account, connect } = useWallet();
  const [remainingAmount, setRemainingAmount] = useState();
  const [contract,setContract] = useState();
  const [maxMint,setMaxMint] = useState()
  const [price,setPrice] = useState(0)
  const [isOpen,setOpen] = useState(false)

  
  const handleMint = async () => {

    await contract.methods.mint(value).send({
      from: account,
      value: Web3.utils.toWei((priceForNft).toString()),
    });
  };

  const [noOfNFT, setNoOfNFT] = useState(null);

 


  const renderButton = ()=>{
    if(isOpen){
       return <ButtonBack>
    <Button onClick={() => handleMint()}>MINT</Button>
    </ButtonBack>
    }else{
return <ButtonBack>
    <Button >Mint not Started</Button>
  </ButtonBack>
    }
  }

  const loadData = async()=>{
      const _web3 =  new Web3(Web3.givenProvider);
      const _contract = new _web3.eth.Contract(ABI,config.nftContract);
      setContract(_contract);
      let totalSupply = await _contract.methods.totalSupply().call()
        console.log("totttt",totalSupply)


    let isOpen = await _contract.methods.saleIsActive().call()
    setOpen(isOpen)
    let _price = (await _contract.methods.alistPrice().call())/1e18
     setPrice(_price)
      const _max = await  _contract.methods.maxAlistMintPerTxn().call()
      setMaxMint(_max)
      setRemainingAmount(10000-totalSupply)
  }
 
  useEffect(()=>{
    if(account){
        loadData()
    }
  },[account])




  useEffect(()=>{
    if(noOfNFT){
        setpriceForNft(noOfNFT * price);
    }
  },[noOfNFT])
  return (
    <BigCard>
    

    <BoxContainer>
      <SmBox>
        <Upper>
          <FaMinusCircle
            className="iconL"
            onClick={() => {
              if (noOfNFT > 0) {
                setNoOfNFT(noOfNFT - 1);
              }
            }}
          />

          <input
            placeholder="No.s of NFT"
            value={noOfNFT}
            style={{
              width: "70%",
              height: "60%",
              border: "none",
              fontSize: "1.5rem",
              paddingLeft: "0.5rem",
            }}
            onChange={(e) => {
              setpriceForNft(parseInt(e.target.value) * price);
              setNoOfNFT(parseInt(e.target.value));
              setvalue(e.target.value);
            }}
            type="number"
          />

          <FcPlus
            className="iconR"
            onClick={() => {
              if (noOfNFT < 20) {
                setNoOfNFT(noOfNFT + 1);
              }
            }}
          />
        </Upper>
        <Lower>
          <T style={{ margin: "1rem 0" }}>Max {maxMint}</T>
        </Lower>
      </SmBox>
      <BigBox>
        <Left>
          <Tu>Price</Tu>
          <Hu style={{ margin: "0" }}>
            {price} ETH
            {/* {mydata?.price / 10 ** 18 || 0.042} eth */}
          </Hu>
        </Left>
        <Divider />
        <Right>
          <Td>TOTAL COST</Td>
          <Hu style={{ margin: " 0" }}>{priceForNft} + GAS</Hu>
        </Right>
      </BigBox>
    </BoxContainer>

    {account ? (
     renderButton()
    ) : (
      <ButtonBack>
        <Button onClick={() => connect()}>Connect</Button>
      </ButtonBack>
    )}
  </BigCard>
  );
};

export default Page2;
